<template>
  <div>
    <b-card
      no-body
      class="card-statistics"
    >
      <b-card-body class="statistics-body">
        <b-row>
          <b-col
            xl="4"
            md="4"
            sm="4"
            class="mb-2 mb-xl-0"
          >
            <b-media no-body>
              <b-media-aside

                class="mr-2"
              >
                <b-avatar
                  size="48"
                  variant="light-success"
                >
                  <feather-icon
                    size="24"
                    icon="HomeIcon"
                  />
                </b-avatar>
              </b-media-aside>
              <b-media-body class="my-auto">
                <h4 class="font-weight-bolder mb-0">
                  {{ aforoTotal - aforoSubTotal }}
                </h4>
                <b-card-text class="font-small-3 mb-0">
                  Disponible
                </b-card-text>
              </b-media-body>
            </b-media>
          </b-col>
          <b-col
            xl="4"
            sm="4"
            md="6"
            class="mb-2 mb-xl-0"
          >
            <b-media no-body>
              <b-media-aside

                class="mr-2"
              >
                <b-avatar
                  size="48"
                  variant="light-danger"
                >
                  <feather-icon
                    size="24"
                    icon="HomeIcon"
                  />
                </b-avatar>
              </b-media-aside>
              <b-media-body class="my-auto">
                <h4 class="font-weight-bolder mb-0">
                  {{ aforoSubTotal }}
                </h4>
                <b-card-text class="font-small-3 mb-0">
                  Ocupados
                </b-card-text>
              </b-media-body>
            </b-media>
          </b-col>
          <b-col
            xl="4"
            md="4"
            sm="4"
            class="mb-2 mb-xl-0"
          >
            <b-media no-body>
              <b-media-aside

                class="mr-2"
              >
                <b-avatar
                  size="48"
                  variant="light-info"
                >
                  <feather-icon
                    size="24"
                    icon="UsersIcon"
                  />
                </b-avatar>
              </b-media-aside>
              <b-media-body class="my-auto">
                <h4 class="font-weight-bolder mb-0">
                  {{ aforoTotal }}
                </h4>
                <b-card-text class="font-small-3 mb-0">
                  Capacidad total
                </b-card-text>
              </b-media-body>
            </b-media>
          </b-col>
        </b-row>
      </b-card-body>
    </b-card>

    <b-card
      no-body
      class="mb-0"
    >
      <div class="m-2">

        <!-- Table Top -->
        <b-row>

          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <v-select
              v-model="perPage"
              :options="pageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>registros</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                type="search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
                debounce="500"
              />
              <b-button
                v-ripple.400="'rgba(113, 102, 240, 0.15)'"
                v-b-modal.modal-residencia-form
                variant="primary"
              >
                <span class="text-nowrap">Agregar</span>
              </b-button>
            </div>
          </b-col>
        </b-row>

      </div>

      <!-- Main table element -->
      <b-table
        id="my-table"
        ref="refTablaHabitaciones"
        class="position-relative"
        show-empty
        head-variant="dark"
        hover
        small
        responsive
        :tbody-tr-class="rowClass"
        :busy.sync="isBusy"
        :items="myProvider"
        :no-provider-filtering="true"
        :fields="fields"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>
        <!-- Column: User -->
        <template #cell(persona_usuario)="data">
          <!-- <pre>{{ data.item.persona_usuario }}</pre> -->
          <b-media vertical-align="center">
            <template #aside>
              <b-avatar
                size="32"
                :variant="`light-${resolveUserServicioVariant(data.item.persona_usuario.nombres)}`"
                :text="avatarText(data.item.persona_usuario.nombres)"
              />
            </template>
            <b-link
              class="font-weight-bold d-block text-nowrap"
            >
              {{ data.item.persona_usuario.nombre_completo }}
            </b-link>
            <small class="text-muted">N° {{ data.item.persona_usuario.numero_documento }}</small> <br>
            <small class="text-muted">CEL: {{ data.item.persona_usuario.celular }}</small>
          </b-media>
        </template>
        <!-- Column: Status -->
        <template #cell(estado)="data">
          <b-badge
            pill
            :variant="`${resolveCitaStatusVariant(data.item.estado)}`"
            class="text-capitalize"
          >
            {{ data.item.estado }}
          </b-badge>
        </template>
        <!-- Column: Actions -->
        <template #cell(acciones)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >

            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <b-dropdown-item
              @click="editarAsistencia(data.item)"
            >
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>
            <b-dropdown-item
              v-b-modal.modal-1
              @click="$router.push({ name: 'residencia-ambientes-becarios-list', params: { id: data.item.id }})"
            >
              <feather-icon icon="FileTextIcon" />
              <span class="align-middle ml-50">Ver detalle</span>
            </b-dropdown-item>

            <b-dropdown-item
              @click="deleteItem(data.item)"
            >
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50">Eliminar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

      </b-table>

      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >

            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>
        </b-row>
      </div>

    </b-card>

    <ambiente-form-modal
      :edited-item="editedItem"
      @refresh-data-habitaciones-table="actualizarTabla"
    />
  </div>
</template>

<script>
import {
  BCard, BTable, BSpinner, BBadge, VBModal, BRow, BCol, BFormInput, BDropdown, BDropdownItem, BPagination, BButton, BMedia, BAvatar,
  BLink, BCardText, BCardBody, BMediaAside, BMediaBody,
} from 'bootstrap-vue'

import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import store from '@/store'
import Ripple from 'vue-ripple-directive'
import { avatarText } from '@core/utils/filter'
// import BuscarPersona from '../BuscarPersona.vue'
import ambienteStoreModule from './ambienteStoreModule'
import AmbienteFormModal from './AmbienteFormModal.vue'

export default {
  components: {
    BTable,
    // BCardImg,
    // BCardHeader,
    // BCardTitle,
    BCardText,
    BCardBody,
    BMediaAside,
    BMediaBody,
    AmbienteFormModal,
    BBadge,
    vSelect,
    BSpinner,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BDropdown,
    BDropdownItem,
    BPagination,
    BMedia,
    BAvatar,
    BLink,

    // componentes local
    // BuscarPersona,
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  data() {
    return {
      aforoTotal: 0,
      aforoSubTotal: 0,
      persona: {},
      editedItem: {},
      editedItemHabitaciones: {},
      isBusy: false,
      mostrar: false,
      items: [],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 100,
      pageOptions: [50, 100, 300, 500],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
      selectedPersona: {},
    }
  },
  created() {
    this.calcularAforoTotal()
  },
  methods: {
    rowClass(item, type) {
      if (!item || type !== 'row') return

      // eslint-disable-next-line consistent-return
      if (item.aforo_total <= item.aforo_subtotal && item.estado === 'activo') return 'table-danger'
    },
    calcularAforoTotal() {
      this.$http.get('residencia/habitaciones/aforo-total').then(response => {
        this.aforoTotal = response.data.data[0].aforoTotal
        this.aforoSubTotal = response.data.data[0].aforoSubTotal
      })
    },
    editarAsistencia(item) {
      this.editedItem = { ...item }
      this.$root.$emit('bv::show::modal', 'modal-residencia-form', '#refModalResidenciaForm')
    },

    deleteItem(item) {
      this.$swal({
        title: '¿Estas seguro?',
        text: '¡No podrás revertir esto!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, bórralo!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('habitaciones/deleteHabitacion', item)
            .then(() => {
              this.$swal({
                icon: 'success',
                title: 'Eliminado!',
                text: 'Tu cita ha sido eliminado.',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
              this.actualizarTabla()
            }).catch(error => {
              this.$swal({
                title: 'Error',
                text: `${error}`,
                icon: 'error',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
            })
        } else if (result.dismiss === 'cancel') {
          this.$swal({
            title: 'Cancelado',
            text: 'Tu visita está seguro :)',
            icon: 'error',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
        }
      })
    },
    actualizarTabla() {
      this.filter = null
      this.$refs.refTablaHabitaciones.refresh()
      this.calcularAforoTotal()
    },
    async myProvider(ctx) {
      const promise = store.dispatch('habitaciones/fetchHabitaciones', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data

        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0

        this.to = to || 0
        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    },
  },
  setup() {
    const AMBIENTES_RESIDENCIA_APP_STORE_MODULE_NAME = 'habitaciones'

    // Register module
    if (!store.hasModule(AMBIENTES_RESIDENCIA_APP_STORE_MODULE_NAME)) store.registerModule(AMBIENTES_RESIDENCIA_APP_STORE_MODULE_NAME, ambienteStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(AMBIENTES_RESIDENCIA_APP_STORE_MODULE_NAME)) store.unregisterModule(AMBIENTES_RESIDENCIA_APP_STORE_MODULE_NAME)
    })

    const fields = [
      {
        key: 'residencia.nombre', label: 'albergue', class: 'text-uppercase', sortable: true,
      },
      {
        key: 'codigo', label: 'hab. cod.', class: 'text-uppercase', sortable: true,
      },
      {
        key: 'tipo.nombre', label: 'tipo', class: 'text-uppercase', sortable: true,
      },
      {
        key: 'aforo_total', label: 'aforo total', class: 'text-uppercase text-center', sortable: true,
      },
      {
        key: 'aforo_subtotal', label: 'aforo', class: 'text-uppercase', sortable: true,
      },
      { key: 'estado', sortable: true },
      { key: 'acciones' },
    ]

    const resolveUserServicioVariant = val => {
      if (val === 'FARMACIA') return 'primary'
      if (val === 'ODONTOLOGÍA') return 'warning'
      if (val === 'OFTAMOLOGÍA') return 'success'
      if (val === 'TÓPICO') return 'info'
      if (val === 'GINECO-OBSTÉTRICO') return 'danger'
      return 'primary'
    }
    const resolveCitaStatusVariant = val => {
      if (val === 'activo') return 'success'
      if (val === 'mantenimiento') return 'warning'
      if (val === 'inhabitable') return 'danger'
      return 'primary'
    }
    return {
      resolveCitaStatusVariant,
      avatarText,
      fields,
      resolveUserServicioVariant,
    }
  },
}
</script>
<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
</style>
